@import url(https://fonts.googleapis.com/css2?family=Ruslan+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&family=Rokkitt:wght@300&family=Ruslan+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background: url("https://images.unsplash.com/photo-1555396273-367ea4eb4db5?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1000&q=80")
  no-repeat center center fixed;
  background-size: cover;
}

main {
  width: 100%;
  min-height: 110vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

* {
  margin: 0;
  padding: 0;
}

.cursor-pointer {
  cursor: pointer;
}

.search-filter-btn {
  width: 20%;
  height: auto;
  font-size: 1em;
  background-color: white;
  border-radius: 0.5em;
}

.search-filter-btn:hover {
  background-color: rgb(82, 179, 211);
}

nav {
  width: 100%;
  height: 5em;
  background-color: rgb(82, 179, 211);
  display: flex;
  align-items: center;
}

.logo {
  color: white;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-family: 'Ruslan Display';
  font-style: normal;
  font-weight: normal;
  font-size: 42px;
  line-height: 39px;
  margin-left: 1em;
  -webkit-text-fill-color: white;
  -webkit-text-stroke-color: black;
  -webkit-text-stroke-width: 1px;
}

.logo:hover {
  color: black;
  -webkit-text-fill-color: black;
  -webkit-text-stroke-color: white;
  -webkit-text-stroke-width: white;
}
.search-container {
  width: 70%;
  min-height: 10em;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: white;
  padding: 1em;
  opacity: 0.9;
  border-radius: 1em;
}

.search-form {
  width: 80%;
  height: auto;
  display: flex;
  justify-content: space-between;
  margin-top: 1em;
}

.search-input {
  width: 70%;
  height: 3em;
  background-color: white;
  border-radius: 0.5em;
}

.search-input::-webkit-input-placeholder {
  position: relative;
  left: 0.5em;
}

.search-input:-ms-input-placeholder {
  position: relative;
  left: 0.5em;
}

.search-input::-ms-input-placeholder {
  position: relative;
  left: 0.5em;
}

.search-input::placeholder {
  position: relative;
  left: 0.5em;
}

.filter-form {
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin-top: 1em;
}

.genre-state-select-container {
  width: 40%;
}

.filter-select {
  width: 80%;
  height: 2em;
}
.table-container {
  width: 70%;
  height: auto;
  min-height: 30em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  opacity: 0.9;
  border-radius: 1em;
  padding: 1em;
  margin: 1em 0 2em 0;
}

.table-header {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 1em;
}

/* table styles */
.restaurant-table {
  width: 90%;
}

.error-container {
  justify-content: center;
}

.restaurant-table-row {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.restaurant-table-data-cell {
  border: 0.25px solid black;
  min-height: 2em;
  text-align: center;
}

.name-cell {
  min-width: 30%;
}

.city-cell {
  min-width: 20%;
}

.state-cell {
  min-width: 5%;
}

.telephone-cell {
  min-width: 15%;
}

.genre-cell {
  min-width: 30%;
}


/* page numbers styles */
.page-numbers-container {
  width: 50%;
  display: flex;
  justify-content: space-around;
  margin-top: 1em;
}

li {
  width: 20%;
  list-style-type: none;
}

.page-button {
  width: 100%;
  font-size: 1em;
  background-color: white;
  border-radius: 0.5em;
}

.page-button:hover {
  background-color: rgb(82, 179, 211);
}

.active {
  background-color: rgb(23, 155, 199);
  color: white;
}

